export function tripHistoryFetched(message) {
  return {
    type: "DATA_RECIEVED",
    payload: message
  };
}

export function fetchInfo() {
  return dispatch => {
    fetch(`/api/hello`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      }
    })
      .then(resp => resp.json())
      .then(data => {
        dispatch(tripHistoryFetched(data.message));
      })
      .catch(e => e);
  };
}



